body{
  background-color: #b5b3b9;
}
.App{
  background-color: #714fc0;
  min-height:700px;
  width: 500px;
  margin: 40px auto;
  border-radius: 10px;
}

#to-do-form input{
  background-color: rgb(209, 218, 221);
  border: 0; 
  color: rgb(26, 22, 22);
  width: 300px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder{
  color: rgba(102, 55, 55, 0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 90px;
  border-radius: 5px;
  border: 0;
  background-color:rgb(11, 228, 83); 
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}